        margin-left: 50px;
    }
    .borderMom {
        border: 1px solid none;
        background-color:rgba(107, 106, 106, 0.281);
        border: 1px solid white;
        height: 357px;
        border-radius: 20px;
    }
    .botSection {
        justify-content:space-between;
    }
    
    .borderBot {
        border-right: 1px solid  none;
        height: 89%;
    }
    
    .field {
        width: 80%;
        height: 25px;
        border-radius: 5px;
        border: 1px solid;
    }
    .textArea {
        width: 80%;
        height: 75px;
        padding-left: 40px;
        border-radius: 3px;
        border: 1px solid;
    }
    
    .fontSize {
        font-size: 90%;
    }
    
    .button {
        width: 75%;
        height: 30px;
        border-radius: 6px;
        border: 1px solid;
    }
    
    .ressources {
        text-align: justify;
        margin: 10px 10px 10px 10px;
        color: white;
        font-size: 95%;
    }
    .marginTop {
